<script>
  import moment from 'moment'

  export default {
    name: 'News',
    props: ['apiKey'],
    data: () => {
      return {
        url: '',
        currentPage: 1,
        totalResults: 0,
        newsPerPage: 10,
        articles: [],
        search: '',
       
      }
    },
    filters: {
      formatDate: function (value) {
        if (value) {
          return moment(String(value)).format('MM/DD/YYYY hh:mm')
        }
      },
    },
    methods: {
      fetchData() {
        if (this.search !== '') {
          this.url =
            'https://newsapi.org/v2/everything?q=' +
            this.search +
            '&pageSize=' +
            this.newsPerPage +
            '&apiKey=' +
            this.apiKey
        } else {
          this.url =
            'https://newsapi.org/v2/top-headlines?country=gb&pageSize=' +
            this.newsPerPage +
            '&apiKey=' +
            this.apiKey

          this.search = ''
        }

        this.articles = []

        let request = new Request(this.url + '&page=' + this.currentPage)
        fetch(request)
          .then((response) => response.json())
          .then((data) => {
            this.totalResults = data.totalResults
            data.articles.forEach((element) => {
              this.articles.push(element)
            })
          })
          .catch((error) => {
            console.log(error)
          })
      },

      selectData(category) {
         
         this.url =
            'https://newsapi.org/v2/top-headlines?category=' +
            category +
            '&apiKey=' +
            this.apiKey

      

        this.articles = []

        let request = new Request(this.url + '&page=' + this.currentPage)
        fetch(request)
          .then((response) => response.json())
          .then((data) => {
            this.totalResults = data.totalResults
            data.articles.forEach((element) => {
              this.articles.push(element)
            })
          })
          .catch((error) => {
            console.log(error)
          })
      },

      updateNewsPerPage(value) {
        this.newsPerPage = value
        this.currentPage = 1
        this.fetchData()
      },

      updatePage(page) {
        this.currentPage = page
        this.fetchData()
      },

      openLink(url) {
        window.open(url)
      },
    },
    created() {
      this.fetchData()
    },

    selectCategorie () {
      this.categorie = 'https://newsapi.org/v2/top-headlines/sources?category=businessapiKey=API_KEY';
      this.fetchData()
    },
  }
</script>

